nav {
  padding: 1rem 25%;
  margin: 0 auto;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
  background: var(--text-color);
}

nav a {
  text-decoration: none;
  text-transform: uppercase;
  color: var(--background-color);
}

#image {
  width: 100%;
  height: 300px;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transition: background-image 0.7s ease-in-out;
  background-image: var(--image);
}

nav a:hover {
  color: rgb(12, 216, 189);
}

h1 {
  text-align: center;
  margin: 3rem 0 2rem 0;
}

p {
  width: 40%;
  margin: 1rem auto;
  line-height: 1.6;
}

#darkMode {
  background: transparent url(/static/media/Darkmode.98c06726.svg) no-repeat center;
  margin-top: 7px;
  width: 70px;
  height: 45px;
  border: none;
  transition: 0.3s ease-in-out;
}

#darkMode:hover,
#darkMode:focus {
  -webkit-filter: none;
          filter: none;
  cursor: pointer;
}

#darkMode.clicked {
  -webkit-filter: none ;
          filter: none ;
  background: transparent url("/static/media/light mode.407076ad.svg") no-repeat center;
}

